//由于直接引用的img不能在canvas里不能toDataURL();
//所以在这里把两个必要结合的身体图放出来，以便结合。

const oneBodySrc = "data:image/png;base64,";

const headLessBodyGroupSrc = "data:image/png;base64,"



function createUntaintImage(src) {
   let img = document.createElement("img")
   img.src = src;
   return img;
}
let oneBodyImg = createUntaintImage(oneBodySrc)
let headLessBodyGroupImg = createUntaintImage(headLessBodyGroupSrc)

module.exports = {
   oneBodyImg,
   headLessBodyGroupImg,
}